<template>
  <section class="banner_out position-relative">
    <div class="banner_box overflow-hidden">
      <div class="banner">
        <swiper
          :modules="modules"
          :slides-per-view="1"
          :space-between="30"
          :pagination="{type: 'bullets', clickable: true, renderBullet}"
          :autoplay="{delay: 5000, disableOnInteraction: false}"
          :observer="true"
          :observeParents="true"
          :speed="1500"
          class="default-slider"
        >
          <swiper-slide v-for="item in slide" :key="item">
            <picture>
              <source :srcset="`/img/${item.photo_small}`" media="(max-width: 991px)" />
              <img :src="`/img/${item.photo_big}`" alt="" class="w-100"/>
            </picture>
          </swiper-slide>
        </swiper>
      </div>
      <div class="banner_info d-none d-md-block">
        <div class="banner_info_title pt-sm-4 mb-sm-5 mb-4">【品牌故事】</div>
        <div class="name text-center mb-3">吉祥物 / 劉爺爺</div>
        <div>
          宜蘭的「劉記花生」，自1975年創立至今，即以當地盛產的花生製作成酥糖產品聞名，將往日在家中廚房炒花生的幸福時光，原汁原味與大眾分享，純手工專業低溫烘焙，嚴選無添加之頂級食材，製作出屬於傳承與堅持的懷念好滋味。2010年以宜蘭三星蔥為名，首創「三星蔥花生糖」，充分結合地方特色，採用新鮮食材，濃郁香脆的口感，風靡全台，已成為全國最具代表性的伴手禮之一。
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import { Pagination, Autoplay } from 'swiper'
import { Swiper } from 'swiper/vue/swiper'
import { SwiperSlide } from 'swiper/vue/swiper-slide'
import 'swiper/swiper.min.css'

export default {
  components: {
    Swiper,
    SwiperSlide
  },
  data () {
    return {
      slide: []
    }
  },
  mounted () {
    const vm = this
    const url = '/json/banner.json'
    this.$http.get(url).then((response) => {
      // console.log(response)
      vm.slide = response.data
    })
  },
  setup () {
    const renderBullet = (index, className) => {
      return '<div class="' + className + '"><span>0' + (index + 1) + '</span><span></span></div>'
    }
    return {
      renderBullet,
      modules: [Pagination, Autoplay]
    }
  }
}
</script>
